import { PrismaClient } from '@prisma/client';

const prisma = new PrismaClient();

export async function getTenantsForUser(userId: string) {
  const memberships = await prisma.userTenantMembership.findMany({
    where: {
      userId: userId,
      status: 'APPROVED',
    },
    include: {
      tenant: true,
    },
  });

  return memberships.map((membership: any) => membership.tenant);
}

export async function getTenantById(tenantId: string) {
  return await prisma.tenant.findUnique({
    where: { id: tenantId },
    include: {
      settings: true,
      branding: true,
    },
  });
}

export async function getUserById(userId: string) {
  return await prisma.user.findUnique({
    where: { id: userId },
    include: {
      profile: true,
      privacySettings: true,
      accountSettings: true,
    },
  });
}

export async function getEventsForTenant(tenantId: string) {
  return await prisma.event.findMany({
    where: { tenantId },
    orderBy: { startDateTime: 'asc' },
  });
}

export async function getPostsForTenant(tenantId: string) {
  return await prisma.post.findMany({
    where: { tenantId, isPublished: true },
    orderBy: { publishedAt: 'desc' },
    include: {
      author: {
        include: {
          profile: true,
        }
      }
    }
  });
}

export async function getMembershipForUserInTenant(userId: string, tenantId: string) {
  return await prisma.userTenantMembership.findUnique({
    where: {
      userId_tenantId: {
        userId,
        tenantId,
      }
    },
  });
}
