import { getServerSession } from 'next-auth/next';
import { authOptions } from './api/auth/[...nextauth]/route';
import { redirect } from 'next/navigation';
import { PrismaClient } from '@prisma/client';
import TenantSelector from './components/tenant/TenantSelector';
import { getTenantsForUser } from '@/lib/data'; // This function needs to be created
import LandingPage from './components/landing/LandingPage';

const prisma = new PrismaClient();

export default async function Page() {
  const session = await getServerSession(authOptions);

  if (!session || !session.user) {
    // For now, we'll show the landing page. Later we can redirect to login.
    return <LandingPage onNavigateToLogin={() => redirect('/auth/login')} onSearch={(term) => redirect(`/explore?q=${term}`)} />;
  }

  const userId = (session.user as any).id;
  const tenants = await getTenantsForUser(userId);
  const allTenants = await prisma.tenant.findMany();


  // This is a placeholder for the tenant selection logic
  return (
    <div className="min-h-screen bg-gray-50 flex items-center justify-center">
        <div className="max-w-2xl w-full">
            <h1 className="text-2xl font-bold text-center mb-4">Welcome, {session.user.name}</h1>
            <TenantSelector tenants={allTenants} onSelect={(tenantId) => redirect(`/tenants/${tenantId}`)} onCreateNew={() => redirect('/tenants/new')} />
        </div>
    </div>
  );
}
